body {
  background: #6b6b76;
  color: #2b2727;
  margin: 10;
  padding: 0;
  font-family: sans-serif
}

h1 {
  font-size: 16px;
  line-height: 20px
}

h2 {
  font-size: 12px;
  line-height: 15px;
  font-style: italic;
  font-weight: 400
}

.rv-xy-plot {
  position: relative
}

line {
  stroke: #d6d6d5!important
}

path.domain {
  stroke: #939393!important
}

.rv-xy-plot__axis__labels text {
  fill: #939393!important
}

.rv-hint {
  background: #494949;
  color: #f8f8f9;
  font-size: 10px;
  padding: 10px;
  width: 90px
}

.rv-hint--orientation-topleft {
  -webkit-transform: translate(-7px,-7px);
  -ms-transform: translate(-7px,-7px);
      transform: translate(-7px,-7px)
}

.rv-hint--orientation-topright {
  -webkit-transform: translate(7px,-7px);
  -ms-transform: translate(7px,-7px);
      transform: translate(7px,-7px)
}

.rv-hint--orientation-bottomleft {
  -webkit-transform: translate(-7px,7px);
  -ms-transform: translate(-7px,7px);
      transform: translate(-7px,7px)
}

.rv-hint--orientation-bottomright {
  -webkit-transform: translate(7px,7px);
  -ms-transform: translate(7px,7px);
      transform: translate(7px,7px)
}


